@use '/src/styles/variables' as *;
@use '/src/styles/mixings' as *;
.container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: $white;
  padding: 20px 0;
  &__title {
    font-size: 24px;
    margin-bottom: 60px;
  }
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 14px 40px;
    @include breakpoints(mobile) {
      grid-template-columns: repeat(1, 1fr);
      grid-gap: 0px 0px;
    }
  }
}
